<template>
<section>
    <div class="hswiper">
        <swiper :options="swiperOption" class="home_swiper">
            <swiper-slide><img src="http://hndqhp.com/Content/UploadFiles/image/20170531/20170531192745_7596.jpg"></swiper-slide>
            <swiper-slide><img src="http://hndqhp.com/Content/UploadFiles/image/20170523/20170523160451_3810.jpg"></swiper-slide>
            <swiper-slide><img src="http://hndqhp.com/Content/UploadFiles/image/20170531/20170531192753_4348.jpg"></swiper-slide>
        </swiper>
        <div class="swiper-pagination"></div> 
    </div>
</section>
</template>
 
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
    data(){
        return {
            //
            swiperOption:{
                //
                pagination: {
                    el: '.swiper-pagination',
                    clickable:true
                },
                //  
                // direction : 'vertical',
                //
                autoplay: {
                    disableOnInteraction: false,
                    delay:2500
                },
                //
                loop:true,
                //
                paginationClickable :true,
                prevButton:'.swiper-button-prev',
                nextButton:'.swiper-button-next',
                //1auto
                slidesPerView:1,
                //Swipertrue
                mousewheel:true ,
                //falseslidewrappertruefreeslide
                // freeMode:true 
            }
        }
    },
    components:{
        swiper,
        swiperSlide
    }
}   
</script>

<style lang="css">
@import "~swiper/dist/css/swiper.css";
.hswiper{
    height: 300px;
}   
.home_swiper img{
    height: 300px;
    width: 100%;
}
.swiper-pagination{
    position: relative;
    margin-top: -50px;
}
.swiper-pagination-bullet{
    width: 100px;
    height: 12px;
    margin-left: 5px;
    border-radius: 0;
    background: #fdfdf0;
}
.swiper-pagination-bullet-active{
    background: #ffe973;
}

</style>


